import request from '@/utils/request'
// 根据门店ID停用/启用
export function invalidShopRoomById (id,type) {
  return request({
    url: `/sys/shopRoom/invalidShopRoomById?shopRoomId=${id}&invalid=${type}`,
    method: 'get',
  })
}
// 分页查询门店
export function selectShopRoomList(data) {
  return request({
    url: `/sys/shopRoom/selectBusinessShopRoomList`,
    method: 'post',
    data: data
  })
}
// 创建店铺
export function addShopRoom(data) {
  return request({
    url: `/sys/shopRoom/addShopRoom`,
    method: 'post',
    data: data
  })
}
// 查询门店详情
export function findShopRoomByIds(id) {
  return request({
    url: `/sys/shopRoom/findShopRoomByIds?shopRoomIds=${id}`,
    method: 'get'
  })
}
// 查询门店详情
export function findShopRoomById(id) {
  return request({
    url: `/sys/shopRoom/findShopRoomById?shopRoomId=${id}`,
    method: 'get'
  })
}
// 编辑门店
export function editShopRoom(data) {
  return request({
    url: `/sys/shopRoom/editShopRoom`,
    method: 'post',
    data: data
  })
}
