<template>
  <div class="pageContent">
    <el-card class="box-card">
      <div class="pageHead">门店信息</div>
      <div>
        <el-form :model="ruleForm"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="门店名称：" required>
            <el-input style="width:400px;" v-model.trim="ruleForm.roomName" maxlength="20" show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="联系电话：" required>
            <el-input style="width:400px;" v-model.trim="ruleForm.telephone" maxlength="11" show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="门店地址：" required>
            <div class="flex-row-start">
              <el-form-item style="margin-right:10px;">
                <el-select @change="address1" value-key="code" v-model="ruleForm.province" placeholder="请选择省">
                  <el-option v-for="item in addressList1" :key="item.code" :label="item.name" :value="item"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item style="margin-right:10px;">
                <el-select @change="address2" value-key="code" v-model="ruleForm.city" placeholder="请选择市">
                  <el-option v-for="item in addressList2" :key="item.code" :label="item.name" :value="item"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="ruleForm.area" value-key="code" placeholder="请选择区">
                  <el-option v-for="item in addressList3" :key="item.code" :label="item.name" :value="item"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-form-item>
          <el-form-item label="详细地址：" required>
            <el-input type="textarea" style="width:400px;" v-model.trim="ruleForm.locationDesc" maxlength="50" show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="门店导航：" required>
            <div class="mapBox amap-page-container">
              <div  :style="{width:'100%',height:'400px'}">
                <el-amap-search-box 
                  class="search-box" 
                  :search-option="searchOption" 
                  :on-search-result="onSearchResult"></el-amap-search-box>
                <el-amap 
                  ref="map" 
                  vid="amap" 
                  :plugin="plugin" 
                  class="amap-demo" 
                  :center="center"
                  :events="events"
                >
                  <el-amap-text 
                    v-for="(text,index) in markers" :key="'text'+index" 
                    :text="text.text" 
                    :offset="text.offset" 
                    :position="text.position"
                  ></el-amap-text>
                  <el-amap-marker 
                    v-for="(marker,index) in markers" :key="'marker'+index"
                    :position="marker.position" 
                    :events="marker.events"
                  ></el-amap-marker>
                </el-amap>
              </div>
            </div>
          </el-form-item>
          <el-form-item label="接待时间：" required>
            <el-button type="primary" :disabled="editNow" @click="addTimer">添加时间段</el-button>
            <div class="timePick" v-for="(item,index) in ruleForm.hourOperate" :key="index">
              <el-time-picker
                :disabled="item.type == 'finish'"
                is-range
                v-model="item.times"
                value-format="HH:mm"
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                placeholder="选择时间范围"
                :picker-options="{
                  format: 'HH:mm'
                }"
              >
              </el-time-picker>
              <el-select :disabled="item.type == 'finish'" style="width:350px;" v-model="item.selectDay" multiple placeholder="请选择">
                <el-option
                  v-for="item in days"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  :disabled="item.disabled">
                </el-option>
              </el-select>
              <el-button v-if="item.type == 'edit'" type="primary" @click="getTimerData(item)">确定</el-button>
              <el-button @click="editTimerData(item,index)">删除</el-button>
            </div>
          </el-form-item>
          <el-form-item label="门店照片：" required>
            <div>
              <el-button :disabled="goodsImg.length >= 3" type="primary" @click="showImgMaterialDia">添加图片<i class="el-icon-upload el-icon--right"></i></el-button>
              <span style="color:#909399;">已上传{{goodsImg.length}}张，还可上传{{3-goodsImg.length}}张</span>
              <div style="color:#909399;">建议尺寸750*350像素，你可以拖拽图片调整顺序，最多上传5张，仅支持jpg、png、gif格式</div>
              <div style="color:#F56C6C;">最少需要添加一张商品图片</div>
            </div>
            <div class="flex-row-start" style="padding:20px 0;" v-if="goodsImg.length >= 1">
              <div class="li" v-dragging="{ item: li, list: goodsImg, group: 'index' }" v-for="(li,index) in goodsImg" :key="index">
                <el-popover placement="bottom" trigger="hover">
                  <el-button @click.stop="deleteImg(index)" type="text" style="color:#909399;" icon="el-icon-circle-close">删除</el-button>
                  <el-image slot="reference" style="width: 150px; height: 150px" :src="li.imageUrl" fit="fill"></el-image>
                </el-popover>
              </div>
            </div>
          </el-form-item>
          <el-form-item label="门店描述：">
            <el-input style="width:400px;" type="textarea" placeholder="请输入门店描述" v-model="ruleForm.roomDesc" maxlength="35" show-word-limit></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="subLoading" @click="onSubmit">保存</el-button>
            <el-button @click="backPage">取消</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 图片库 -->
    <el-dialog title="图片库" :visible.sync="showImgMaterial" width="70%">
      <imageMaterial
        :limitType="true"
        :getPost="getPost"
        :multiple="false"
        :totalNum="3"
        :selectNum="this.goodsImg.length"
        @onlyclose="onlyclose"
        @subMit="subMit"
      ></imageMaterial>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getAddressLevel } from '@/api/shop/createShop'
import { findShopRoomById,editShopRoom } from '@/api/shop/shopManage'
import imageMaterial from '@/components/shopMaterial/imageMaterial';
export default {
  data(){
    const self = this;
    return{
      subLoading:false,
      checkDays:[],
      days:[
        {
          value:'0',
          label:'周一',
          disabled:false
        },
        {
          value:'1',
          label:'周二',
          disabled:false
        },
        {
          value:'2',
          label:'周三',
          disabled:false
        },
        {
          value:'3',
          label:'周四',
          disabled:false
        },
        {
          value:'4',
          label:'周五',
          disabled:false
        },
        {
          value:'5',
          label:'周六',
          disabled:false
        },
        {
          value:'6',
          label:'周日',
          disabled:false
        }
      ],
      editNow:false,
      center: [121.59996, 31.197646],
      searchOption: {
        city: '',
        citylimit: false
      },
      lng: 0,
      lat: 0,
      trueMaker: true,
      plugin: [
        {
          pName: 'ToolBar',
          events: {
            init(instance) {
              console.log('instance',instance);
            }
          }
        }
      ],
      events: {
        click(e) {
          let { lng, lat } = e.lnglat;
          self.lng = lng;
          self.lat = lat;

          // 这里通过高德 SDK 完成。
          var geocoder = new AMap.Geocoder({
            city: '全国',
            radius: 1000,
            extensions: "all"
          });        
          geocoder.getAddress([lng ,lat], function(status, result) {
            if (status === 'complete' && result.info === 'OK') {
              if (result && result.regeocode) {
                console.log('status',status)
                console.log('result',result)
                // self.address = result.regeocode.addressComponent;
                self.ruleForm.locationDesc = result.regeocode.formattedAddress;
                self.$nextTick();
                let areaCode = result.regeocode.addressComponent.adcode;
                let provinceCode = areaCode.substring(0,2) + "0000";
                let provinceName = result.regeocode.addressComponent.province
                self.ruleForm.province = {
                  name:provinceName,
                  code:provinceCode
                }
                self.ruleForm.provinceCode = provinceCode
                self.ruleForm.provinceName = provinceName

                self.getAddressInput(2,provinceCode)
                let cityName = result.regeocode.addressComponent.city
                if(provinceName == "北京市") cityName = '北京城区'
                if(provinceName == "上海市") cityName = '上海城区'
                if(provinceName == "天津市") cityName = '天津城区'
                let cityCode = areaCode.substring(0,4) + "00";
                self.ruleForm.city ={
                  name:cityName,
                  code:cityCode
                }
                console.log('city',self.ruleForm.city)
                self.ruleForm.cityCode = cityCode
                self.ruleForm.cityName = cityName

                self.getAddressInput(3,cityCode)
                self.ruleForm.areaCode = areaCode
                self.ruleForm.areaName = result.regeocode.addressComponent.district
                self.ruleForm.area ={
                  name:result.regeocode.addressComponent.district,
                  code:areaCode
                }
                self.trueMaker = true
                self.markers = [];
                let obj = {
                  position : [lng,lat],
                  text : result.regeocode.formattedAddress,
                  offset: [0, -60],
                }
                self.markers.push(obj)
              }
            }
          });        
        }
      },
      ruleForm:{
        hourOperate:[],             // 接待时间
      },
      addressList1:[],              // 创建 - 下拉 - 地址1级
      addressList2:[],              // 创建 - 下拉 - 地址2级
      addressList3:[],              // 创建 - 下拉 - 地址3级
      markers: [
        {
          position: ['1','1'],
          text:'',
          offset: [0, -60],
        }
      ],
      goodsImg:[
      ],
      showImgMaterial:false,
      getPost:false,

      timerTimes:[],
      timerDays:[],
    }
  },
  created(){
    this.getAddressInput(1,'')
    this.getData()
  },
  methods:{
    getData(){
      findShopRoomById(this.$route.query.id).then(res=>{
        console.log('门店详情',res)
        res.data.hourOperate = JSON.parse(res.data.hourOperate)
        this.ruleForm = res.data
        let position = res.data.location.split(",")
        this.center = position
        this.markers[0].position = position;
        this.markers[0].text = res.data.locationDesc;
        let picArr = []
        res.data.roomPictures.forEach(e=>{
          let obj ={
            imageUrl : e
          }
          picArr.push(obj)
        })
        this.goodsImg = picArr
        this.ruleForm.province = {
          name:res.data.provinceName,
          code:res.data.provinceCode
        }
        this.getAddressInput(2,res.data.provinceCode)

        this.ruleForm.city = {
          name:res.data.cityName,
          code:res.data.cityCode
        }
        this.getAddressInput(3,res.data.cityCode)

        this.ruleForm.area = {
          name:res.data.areaName,
          code:res.data.areaCode
        }
        // 判断自提时间
        res.data.hourOperate.forEach(e=>{
          e.selectDay.forEach(item => this.checkDays.push(item))
        })
        this.checkDays.forEach(e => this.days[e].disabled = true)
        this.editNow = this.checkDays.length == 7
      })
    },
    editTimerData(e,i){
      e.selectDay.forEach(e => this.days[e].disabled = false);
      this.ruleForm.hourOperate.splice(i, 1)
      this.editNow = false;
    },
    // 接待时间点击确定
    getTimerData(e){
      console.log('e',e)
      if(!e.times || !e.times[0] || !e.times[1]){
        this.$message.error('请选择接待时间');
        return;
      }
      let startDay = Date.parse(`2019-12-31 ${e.times[0]}:00`)
      let endDay = Date.parse(`2019-12-31 ${e.times[1]}:00`)
      if(startDay >= endDay){
        this.$message.error('开始时间应大于结束时间');
        return;
      }
      e.beginTime = e.times[0]
      e.finishTime = e.times[1]
      if(e.selectDay.length == 0){
        this.$message.error('请选择接待日');
        return;
      }
      e.selectDay.forEach(e=>{
        this.days[e].disabled = true
        this.checkDays.push(e)
      });
      e.type = 'finish'
      if(this.checkDays.length != 7){
        this.editNow = false;
      }
    },
    // 添加时间段
    addTimer(){
      this.ruleForm.hourOperate.push({
        beginTime:'06:00',
        finishTime:'23:00',
        selectDay:[],
        times:['06:00','23:00'],
        type:'edit'
      })
      this.editNow = true
    },
    onSubmit(){
      console.log('ruleForm',this.ruleForm)
      if(!this.ruleForm.roomName){
        this.$message.error('请填写门店名称');
        return;
      }
      if(!this.ruleForm.telephone){
        this.$message.error('请填写联系电话');
        return;
      }
      if(!this.ruleForm.province || !this.ruleForm.city || !this.ruleForm.area){
        this.$message.error('请选择门店地址');
        return;
      }
      if(!this.ruleForm.locationDesc){
        this.$message.error('请填写详细地址');
        return;
      }
      if(!this.trueMaker){
        this.$message.error('请在地图上点击导航位置');
        return;
      }
      this.ruleForm.location = this.markers[0].position.join(',')
      if(this.ruleForm.hourOperate.length == 0){
        this.$message.error('请添加接待时间');
        return;
      }
      let erHourArr = this.ruleForm.hourOperate.filter(e => e.type == 'edit')
      console.log('erHourArr',erHourArr)
      if(erHourArr.length > 0){
        this.$message.error('请完善接待时间');
        return;
      }
      
      if(this.goodsImg.length == 0){
        this.$message.error('请上传门店图片');
        return;
      }
      let picArr = []
      this.goodsImg.forEach(e => picArr.push(e.imageUrl))
      this.ruleForm.roomPicture = picArr.join(',')
      this.ruleForm.shopId = this.shop.shopId
      console.log('成功了！',this.ruleForm)
      this.subLoading = true;
      editShopRoom(this.ruleForm).then(res=>{
        console.log('res',res)
        if(res.code == 200){
          this.$message({
            message: '编辑成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.$router.replace({
              path:'/shop/shopManage'
            })
            this.subLoading = false;
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    backPage(){
      this.$confirm('是否放弃编辑?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({
          path:'/shop/shopManage'
        })
      }).catch(() => {});
    },
    onlyclose(e){
      console.log('值点了关闭',e)
      this.showImgMaterial = false;
      this.getPost = false;
    },
    subMit(e){
      console.log('点击了确定',e)
      this.showImgMaterial = false;
      this.getPost = false;
      e.forEach(item => this.goodsImg.push(item))
      console.log('goodsImg',this.goodsImg)
    },
    // 删除图片
    deleteImg(i){
      this.goodsImg.splice(i, 1)
    },
    // 
    showImgMaterialDia(){
      this.getPost = true;
      this.limitType = true;
      this.selectNum = this.goodsImg.length;
      this.imgMaterialType = 'goodsImg'
      this.showImgMaterial = true;
    },
    onSearchResult(pois) {
      let self = this;
      console.log('地图搜索结果',pois)
      let latSum = 0;
      let lngSum = 0;
      if (pois.length > 0) {
        pois.forEach(poi => {
          let {lng, lat} = poi;
          lngSum += lng;
          latSum += lat;
          let obj = {
            position:[poi.lng, poi.lat],
            text:poi.name,
            offset: [0, -60],
          }
          this.markers.push(obj);
        });
        let center = {
          lng: lngSum / pois.length,
          lat: latSum / pois.length
        };
        this.center = [center.lng, center.lat];
      }
    },
    // 创建店铺 - 下拉地址 2级
    address1(val){
      console.log('执行了1',val)
      this.addressList2 = [];
      if(this.ruleForm.city) this.ruleForm.city = '';
      this.addressList3 = [];
      if(this.ruleForm.area) this.ruleForm.area = '';
      this.getAddressInput(2,val.code);
    },
    // 创建店铺 - 下拉地址 3级
    address2(val){
      console.log('执行了2',val)
      this.addressList3 = [];
      if(this.ruleForm.area) this.ruleForm.area = '';
      this.getAddressInput(3,val.code);
    },
    // 创建店铺 - 下拉选择地址
    getAddressInput(id1,id2){
      getAddressLevel(id1,id2).then(res=>{
        console.log(`${id1}级下拉获取下拉框地址`,res)
        if(id1 == 1){
          this.addressList1 = res.data
          return;
        }
        if(id1 == 2){
          this.addressList2 = res.data
          return;
        }
        if(id1 == 3){
          this.addressList3 = res.data
          return;
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
  components: {
    imageMaterial,
  },
}
</script>
<style lang="less" scoped>
  .mapBox{
    box-sizing: border-box;
    padding-right: 300px;
    position: relative;
  }
  .amap-demo {
    height: 400px;
  }
  .search-box {
    position: absolute;
    top: 15px;
    left: 80px;
  }
  .timePick{
    margin-top: 10px;
  }
  .timePick /deep/ .el-range-separator{
    line-height: 22px;
  }
  .tag{
    cursor: pointer;
  }
  .li{
    margin-right:20px;
  }
</style>